<template>
  <div class="PFRBox">
    <div class="title">
        系统角色列表<el-button class="right" icon="el-icon-user">新建系统角色</el-button>
    </div>
    <el-divider></el-divider> 
    <!-- 角色列表 -->
    <template>
        <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column
            prop="date"
            label="角色"
            width="360">
            </el-table-column>
            <el-table-column
            prop="name"
            label="操作">
                <el-button type="warning" plain>编辑</el-button>
                <el-button type="info" plain>用户</el-button>
                <el-button type="danger" plain>删除</el-button>
            </el-table-column>
        </el-table>
    </template>
  </div>
</template>

<script>
export default {
    data(){
        return{
             tableData:[//这是列表
                {
                    date: '2016-05-04',
                    name: '王小虎'
                }
             ]
        }
    }
}
</script>

<style lang="stylus" scoped>
.PFRBox
    padding 30px
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
    .right
        float right
        margin-top -7px
</style>


